header.header {
	padding-top: rem(33);

	display: flex;
	flex-direction: column;
	align-items: center;

	.logo {
		margin-bottom: rem(26);
	}

	.title {
		display: flex;
		flex-direction: column;
		align-items: center;
		margin-bottom: rem(12);

		&__top, &__bottom {
			color: white;
		}

		&__top {
			line-height: 105%;
			font-size: rem(20);
		}

		&__bottom {
			line-height: 108%;
			font-size: rem(24);
			font-weight: bold;
		}
	}
}