footer.footer {
	padding-bottom: rem(40);
	margin-top: auto;

	display: flex;
	flex-direction: column;
	align-items: center;

	background: rgba(255, 255, 255, 0.85);
	box-shadow: 0px -9px 8px rgba(0, 0, 0, 0.25);
	backdrop-filter: blur(5px);
	/* Note: backdrop-filter has minimal browser support */

	border-radius: 19px 19px 0px 0px;

	&__btn {
		margin: 0 rem(42);
	}

	.text {
		padding-top: rem(15);
		padding-left: rem(22);
		padding-right: rem(22);

		margin-bottom: rem(18);

		font-weight: 300;
		color: #3D3D3D;

		text-align: center;
	}

	.button_blue, .button_gray {
		width: 100%;
		display: flex;
		justify-content: center;
	}

	.button_blue {
		margin-bottom: rem(12);
		padding: rem(10) rem(42) rem(13) rem(42);
		
		background: linear-gradient(180deg, #0332E8 0%, #3273FF 100%);
		box-shadow: 0px 4px 13px rgba(13, 7, 33, 0.25);
		border-radius: 44px;

		font-weight: bold;
		color: white;
	}

	.button_gray {
		padding: rem(10) rem(42) rem(13) rem(42);
		background: linear-gradient(180deg, #FFFFFF 0%, #EEEEEE 100%);
		border: 1px solid #FFFFFF;
		box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
		border-radius: 44px;

		color: #3D3D3D;
	}
}