:root {
	// --var: ....
}

* {
	outline: none;
	border: 0;
}

html, body {
	position: relative;
	overflow-x: hidden;
	// height: 100%;
	// overscroll-behavior: none;
}

html {
	font-size: 100%;
	// height: 100%;	
}

button {
	background-color: transparent;
}

a {
	text-decoration: none;
}

li {
	list-style: none;
}

body {
	position: relative;
	font-family: 'Grilroy', sans-serif;
	font-size: rem(14);
	// height: 100%;
	overflow-x: hidden;
	// max-width: 600px;
	// -webkit-overflow-scrolling: touch;

	background: url(../img/bg.jpg) no-repeat top center;
	background-size: cover;

	
}

.container {
	width: calc(100% - 16px);
	margin-left: rem(8);
	margin-right: rem(8);
	min-width: 320px;
	min-height: 100vh;

	display: flex;
	flex-direction: column;
	align-items: center;
}

